*, :before, :after {
  box-sizing: border-box;
}

* {
  font: inherit;
  margin: 0;
  padding: 0;
}

html {
  color-scheme: dark light;
}

body {
  min-height: 100vh;
}

img, picture, svg, video {
  max-width: 100%;
  display: block;
}

:root {
  font-family: Roboto, sans-serif;
  font-weight: normal;
}

* {
  font-size: 12px;
}

body {
  gap: 0 20px;
  display: grid;
}

#header, #main, #footer {
  flex-direction: column;
  display: flex;
}

#main {
  grid-area: main;
}

h2 {
  margin: 1.5rem 0 0;
  font-size: 1.33rem;
  font-weight: 600;
}

a {
  color: #478cf3;
  cursor: pointer;
  font-size: .9rem;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

a.extra {
  color: #999;
  padding-right: 15px;
  font-size: 1rem;
  position: relative;
}

a.extra:after {
  content: "❯";
  position: absolute;
  -webkit-transform: translate(3px, -1px);
  transform: translate(3px, -1px);
}

.warning {
  color: red;
}

#footer {
  color: rgba(0, 0, 0, 0);
  background: linear-gradient(90deg, #478cf3 0%, #a200ff 100%);
  -webkit-background-clip: text;
  background-clip: text;
  grid-area: footer;
  align-content: center;
  justify-content: center;
  padding: 0 10px;
  font-size: smaller;
  font-weight: lighter;
}

.avatar > img {
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
  background: linear-gradient(90deg, #478cf3, #a200ff, #478cf3, #a200ff) 0 0 / 300% 100%;
  border-radius: 50%;
  -webkit-animation: 5s infinite gradient;
  animation: 5s infinite gradient;
}

@-webkit-keyframes gradient {
  100% {
    background-position: 100% 0;
  }
}

@keyframes gradient {
  100% {
    background-position: 100% 0;
  }
}

.avatar_editable {
  cursor: pointer;
  --delay: .5s;
  transition: box-shadow var(--delay);
  border-radius: 50%;
  position: relative;
  box-shadow: 0 0 0 2px #eaeaea;
}

.avatar_editable:hover {
  box-shadow: 0 0 0 2px #478cf3;
}

.avatar_editable:after {
  content: "Изменить аватар";
  height: 100%;
  width: 100%;
  border-radius: inherit;
  color: #fff;
  transition: background-color var(--delay);
  background-color: rgba(0, 0, 0, .5);
  flex-wrap: wrap;
  align-content: center;
  font-size: 1.3rem;
  line-height: 1.3rem;
  display: flex;
  position: absolute;
  top: 0;
}

.avatar_editable:hover:after {
  background-color: rgba(0, 0, 0, .667);
}

.chat-preview {
  color: #999;
  cursor: pointer;
  grid-template: "avatar message time" 25px
                 "avatar message counter" 25px
                 / 50px minmax(150px, auto) 1fr;
  gap: 0 10px;
  margin: 5px 0;
  padding: 12px 10px;
  display: grid;
  position: relative;
  box-shadow: 0 12px 0 -10px #fbfbfb, 0 13px 0 -10px #eaeaea, 0 -12px 0 -10px #fbfbfb, 0 -13px 0 -10px #eaeaea;
}

.chat-preview.current {
  color: #666;
  background-color: #d0e0fb;
}

.chat-preview:hover {
  background-color: #e4edfd;
}

.chat-preview__avatar {
  grid-area: avatar;
}

.chat-preview__last-message {
  flex-direction: column;
  grid-area: message;
  gap: 4px 0;
  display: flex;
}

.chat-preview__last-message .message__text {
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  white-space: normal;
  -webkit-box-orient: vertical;
  display: -webkit-box;
  overflow: hidden;
}

.message__author {
  color: #1e1e1e;
  font-size: 1.1rem;
  font-weight: bolder;
}

.chat-preview__update-time {
  grid-area: time;
  justify-self: flex-end;
  font-size: .75rem;
}

.chat-preview__new-messages-counter {
  color: #fff;
  width: 1.7em;
  height: 1.7em;
  background-color: #478cf3;
  border-radius: 50%;
  grid-area: counter;
  justify-content: center;
  justify-self: self-end;
  align-items: center;
  font-size: .9rem;
  display: flex;
}

.input-field {
  width: 100%;
  flex-direction: column;
  display: flex;
}

.input-field input:disabled {
  background-color: inherit;
}

.input-field input:hover, .input-field input:focus {
  outline: none;
}

.input-field_theme-standard label {
  color: #999;
  font-size: .75rem;
}

.input-field_theme-standard label + input {
  margin-bottom: 14px;
}

.input-field_theme-standard input {
  border: none;
  border-bottom: 1px solid #478cf3;
  outline: 0;
  padding: 7px 0;
}

.input-field_theme-inline {
  flex-direction: row;
  position: relative;
  overflow: hidden;
}

.input-field_theme-inline input:focus {
  color: #1e1e1e;
}

.input-field_theme-inline:not(:last-child):before, .input-field_theme-inline:not(:last-child):after {
  content: "";
  height: 1px;
  width: 100%;
  transition: -webkit-transform .3s, transform .3s;
  position: absolute;
  bottom: 0;
}

.input-field_theme-inline:before {
  background-color: #eaeaea;
  -webkit-transform: translate(0);
  transform: translate(0);
}

.input-field_theme-inline:not(:last-child):focus-within:before {
  -webkit-transform: translate(-100%);
  transform: translate(-100%);
}

.input-field_theme-inline:after {
  background-color: #478cf3;
  -webkit-transform: translate(100%);
  transform: translate(100%);
}

.input-field_theme-inline:not(:last-child):focus-within:after {
  -webkit-transform: translate(0);
  transform: translate(0);
}

.input-field_theme-inline label, .input-field_theme-inline input {
  padding: 10px 0;
}

.input-field_theme-inline label {
  width: 100%;
}

.input-field_theme-inline input {
  text-align: right;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  color: #999;
  border: none;
  margin-left: auto;
}

.input-field_theme-primary input {
  color: #1e1e1e;
  background-color: #efefef;
  border: none;
  border-radius: 5px;
  outline: none;
  padding: 10px 10px 10px 20px;
}

.input-field_theme-primary input:focus::-webkit-input-placeholder {
  color: rgba(0, 0, 0, 0);
}

.input-field_theme-primary input:focus::placeholder {
