:root {
  --header-height: 100px;
}

body {
  max-height: 100vh;
}

.chatlist {
  background-color: #fbfbfb;
  grid-area: side;
  overflow: hidden;
  box-shadow: inset -2px 0 #fff, inset -3px 0 #eaeaea;
}

.chatlist__header {
  height: var(--header-height);
  flex-direction: column;
  justify-content: space-between;
  padding: 20px 10px 15px;
  display: flex;
}

.chatlist__header a.extra {
  margin-left: auto;
  display: inline;
}

.chatlist__content {
  height: calc(100% - var(--header-height));
  overflow-y: scroll;
}

.intro {
  color: #999;
  margin: auto;
}

.chat, .chat__header, .chat__main, .chat__footer {
  display: flex;
}

.chat__header, .chat__footer {
  height: 50px;
  min-height: 50px;
  flex-direction: row;
  align-items: center;
  gap: 10px;
  margin-right: 20px;
}

.chat {
  height: 100%;
  flex-direction: column;
  justify-content: space-between;
}

.chat__header {
  justify-content: inherit;
  border-bottom: 1px solid #eaeaea;
}

.contact-info {
  align-items: center;
  gap: inherit;
  flex-direction: row;
  display: flex;
}

.contact-info__title {
  color: #1e1e1e;
  font-size: 1.1rem;
  font-weight: bolder;
}

.chat__main {
  flex-direction: column-reverse;
  margin-top: auto;
  margin-right: 10px;
  padding: 30px 7px 30px 0;
  overflow-y: scroll;
}

.chat__main .message {
  max-width: 45%;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  word-wrap: break-word;
  background-color: #f8f8f8;
  border-radius: 12px;
  margin-bottom: 10px;
  padding: 1rem;
  position: relative;
}

.chat__main .message:first-child {
  margin-bottom: 0;
}

.chat__main .message:not([data-author="self"]) {
  border-top-left-radius: 0;
}

.chat__main .message[data-author="self"] {
  background-color: #e4edfd;
  border-bottom-right-radius: 0;
  align-self: flex-end;
}

.message__meta {
  float: right;
  align-items: center;
  margin-left: 15px;
  display: flex;
}

.message__meta > * {
  font-size: .9rem;
}

.message__status {
  margin-right: 5px;
  display: flex;
}

.message__time {
  color: #999;
  position: relative;
}

.chat__main .message[data-type="attach"] {
  max-width: 400px;
  max-height: 300px;
  background-color: #fff;
  border: 3px solid #f8f8f8;
  padding: 0;
}

.message[data-type="attach"][data-author="self"] {
  border-color: #e4edfd;
}

.message[data-type="attach"] > img {
  max-height: 100%;
  border-radius: 9px;
}

.message[data-author="self"][data-type="attach"] > img {
  border-bottom-right-radius: 0;
}

.message:not([data-author="self"])[data-type="attach"] > img {
  border-top-left-radius: 0;
}

.message[data-author="self"] .message__time {
  color: #478cf3;
}

.message[data-type="attach"] .message__meta {
  bottom: 5px;
  top: unset;
  background: rgba(228, 237, 253, .733);
  border-radius: 5px;
  padding: .3em;
  position: absolute;
  right: 5px;
}

.chat__footer {
  border-top: 1px solid #eaeaea;
}

.new-message {
  display: inherit;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  gap: inherit;
}

.new-message__input {
  align-items: center;
  gap: inherit;
  width: 100%;
  flex-direction: row;
  display: flex;
}

.chatlist__content::-webkit-scrollbar, .chat__main::-webkit-scrollbar {
  opacity: 0;
  width: 3px;
}

.chat__main:hover::-webkit-scrollbar-thumb, .chatlist__content:hover::-webkit-scrollbar-thumb {
  background-color: #eaeaea;
}

